export const BASE_URL = "https://api.chernyshevdi.nomoredomains.rocks";

const checkFetch = (res) => {
  if (res.ok) {
    return res.json();
  } else {
    return Promise.reject(`Ошибка: ${res.status}`);
  }
}

export const register = (password, email) => {
  return fetch(BASE_URL + "/signup", {
    credentials: 'include',
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify({
      password: password,
      email: email,
    }),
  }).then(checkFetch);
};

export const login = (password, email) => {
  return fetch(BASE_URL + "/signin", {
    credentials: 'include',
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify({
      password: password,
      email: email,
    }),
  })
    .then(checkFetch)
};

export const tokenCheck = () => {
  return fetch(BASE_URL + "/users/me", {
    credentials: 'include',
    method: "GET",
    headers: {
      "Content-Type": "application/json",
      //authorization: token || localStorage.getItem("token"),
    },
  })
    .then(checkFetch)
    .then((res) => {
      return res;
    })
};
